html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font: inherit;
    vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
    display: block;
}

body {
    width: 100%;
    height: 100%;
    line-height: 1;
    background-color: #f8f8f8;
}

#root {
    width: 100%;
    height: 100%;
}

ol, ul {
    list-style: none;
}

blockquote, q {
    quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
    content: '';
    content: none;
}

table {
    border-collapse: collapse;
    border-spacing: 0;
}
.ant-modal-footer{
    text-align: center !important
}
.dropDownMenu .switch-text {
    font-size: 12px;
    color: #666666;
    display: inline-block;
    margin-right: 10px;
    width: 65px;
}
.dropDownMenu .checked ,.unChecked{
    display: inline-block;
    width: 25px;
    height: 14px;
}
.dropDownMenu .checked {
    background: url('static/imgs/icon.png') no-repeat -372px -31px;
}
.dropDownMenu .unChecked {
    background: url('static/imgs/icon.png') no-repeat -337px -31px;
}

.dropDownMenu .ant-switch {
    width: 30px;
    background-color: #fff;
}
.dropDownMenu .ant-switch::after {
    width: 0;
}
.dropDownMenu .ant-switch-inner {
    margin-left: 0 !important;
    margin-right: 0 !important;
}
.dropDownMenu .ant-switch-checked {
    background-color: #fff;
}

.dropDownMenu .ant-dropdown-menu-item:hover .ant-switch{
    background-color: #f0f8ff;
}

.App {
    height: 100%;
}
.ant-menu-item-disabled > a, .ant-menu-submenu-disabled > a{
    color: #fff !important;
    cursor: not-allowed;
}

.ant-table-thead > tr, .ant-table-tbody > tr {
    height: 40px;
    line-height: 40px;
    text-align: center;
}
.ant-table-thead > tr > th {
    text-align: center !important;
}

 .ant-table-tbody > tr > td ,.ant-table-thead > tr > th {
     padding: 0 !important;
 }
.ant-pagination{
    text-align: center;
    float: none !important;
}

.ant-layout.ant-layout-has-sider > .ant-layout, .ant-layout.ant-layout-has-sider > .ant-layout-content{
    overflow-y: hidden;
}

#components-table-demo-resizable-column .react-resizable {
    position: relative;
    background-clip: padding-box;
}

#components-table-demo-resizable-column .react-resizable-handle {
    position: absolute;
    width: 10px;
    height: 100%;
    bottom: 0;
    right: -5px;
    cursor: col-resize;
    z-index: 1;
}

.ant-popover-inner-content {
    padding: 0 !important;
}

.ant-modal-title{
    font-weight: 700 !important;
}
.hidden-drop-down{
    display:none !important
}
.codeModal .ant-modal-body {
    text-align: center;
}
.statusTitle .ant-table-header-column>div,.activeTitle .ant-table-header-column>div{
    width: 98px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}
.activeTitle {
    /* width:100%; */
}
.dropDownMenu {
    max-height: 350px;
    overflow-y: auto;
}


.col-span {
    display: -webkit-box;
    overflow: hidden;
    text-overflow: ellipsis;
    word-break: break-all;
    /* autoprefixer: off */
    -webkit-box-orient: vertical;
}
li.ant-select-tree-treenode-disabled > span:not(.ant-select-tree-switcher), li.ant-select-tree-treenode-disabled > .ant-select-tree-node-content-wrapper, li.ant-select-tree-treenode-disabled > .ant-select-tree-node-content-wrapper span{
    color:rgba(21, 19, 19, .5) !important
}
.schedule-setting.ant-form-inline .ant-form-item{
    margin-right:0;
}
.schedule-setting .ant-row:nth-child(1) .ant-col:nth-child(2) .ant-form-item-label{
    padding-left:12px;
}
.schedule-setting .ant-form-item-label> label:not(.ant-form-item-required){
    padding-left:12px
}
.plat-setting .ant-form-inline .ant-form-item{
    margin-right:0;
}
.plat-setting .ant-form-inline .ant-col:nth-child(3) .ant-form-item-control{
    width:97%;
}
.tootips {
    position: absolute;
    z-index: 1000;
    max-width: 100%;
    height: 17px;
    background: #fff;
    padding: 0 10px;
    font-size: 12px;
    line-height: 16px;
    border-radius: 2px;
}
.icon-clear {
    background: transparent;
    height: auto;
    border: none;
}
.label-tootips{
    position: absolute;
    /* border: 1px solid #ddd; */
    padding: 3px 10px;
    font-size: 12px;
    background: #000;
    z-index: 10;
    color: #fff;
    border-radius: 2px;
}
.icon-clear.ant-btn-disabled, 
.icon-clear.ant-btn.disabled, 
.icon-clear.ant-btn[disabled], 
.icon-clear.ant-btn-disabled:hover, 
.icon-clear.ant-btn.disabled:hover, 
.icon-clear.ant-btn[disabled]:hover, 
.icon-clear.ant-btn-disabled:focus, 
.icon-clear.ant-btn.disabled:focus, 
.icon-clear.ant-btn[disabled]:focus, 
.icon-clear.ant-btn-disabled:active, 
.icon-clear.ant-btn.disabled:active, 
.icon-clear.ant-btn[disabled]:active, 
.icon-clear.ant-btn-disabled.active, 
.icon-clear.ant-btn.disabled.active, 
.icon-clear.ant-btn[disabled].active{
    background: transparent !important;
}
.icon-clear.ant-btn:hover{
    background: transparent !important;
}